body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  display: flex;
  flex-direction: column;

}


.header {
  display: flex;
  width: 100%;
  background-color: #274C77;
  justify-content: space-around;
  text-align: center;
  align-items: center;
}

.header-item {
  width: 300px;
}

.header-text {
  color: #E7ECEF;
}

.nav-items {
  color: #E7ECEF;
  display: flex;
  list-style: none;
  
  
}

.nav-items li:not(:last-child) {
  border-right: .15vw solid #E7ECEF;
}

.nav-items li a{
  text-decoration: none;
  color: inherit;
  padding: 1vw;
}

.projects-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin: 2vh;
}

.skills-text {
  text-decoration-line: underline;
}

.skill-icon-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1vw;
}

.skill-icon {
  width: 4rem;
  height: 4rem;
}

.project-cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

@media (max-width: 650px) {
  .skill-icon {
    width: 2rem;
    height: 2rem;
  }
}

.project-card {
  display: flex;
  flex-direction: column;
  width: 50rem;
  margin: 3rem;
}

.project-card-image {
  border: .15rem solid black;
  border-radius: 20px;
  width: 100%;
  height: 100%;
}

@media (max-width: 650px) {
  .project-cards-container {
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .project-card {
    width: 20rem;
  }
}

.resume-container {
  background-color: #E7ECEF;
  text-decoration: none;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.resume-container a {
  text-decoration: none;
  color: #8B8C89;
}

.resume-icon {
  height: 20rem;
  width: 20rem;
}

.resume-icon a img {
  height: 100%;
  width: 100%;
}


.contact-container {
  display: flex;
  height: 800px;
  align-items: center;
  justify-content: center;
}

.form-container {
  width: min(90%, 1800px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-info-container {
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-left: .2rem solid #6096BA;
  padding: 10px;
}


.contact-info-container div {
  font-size: 1.25rem;
}

.contact-form-container {
  margin-left: 10%;
  width: 50%;
}

#form-message {
  width: min(90%, 50rem);
  height: 15rem;

}

.form-control {
  margin-top: 1rem;
}

@media (max-width: 650px) {
  .form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  #form-message {
    height: 10rem;
  
  }

  .contact-form-container {
    width: 70%;
  }
}
